export const toTC = function (SC) {
  const scString =
    "";
  const tcString =
    "";
  let str = "";
  for (let i = 0; i < SC.length; i++) {
    if (scString.indexOf(SC.charAt(i)) != -1)
      str += tcString.charAt(scString.indexOf(SC.charAt(i)));
    else str += SC.charAt(i);
  }
  return str;
}